  addCreditCardPayment: 'https://backend-clone.sav-vy.com/oliv-api/add-credit-card',
  addCreditCardPaymentElement: 'https://backend-clone.sav-vy.com/oliv-api/add-credit-card',
  defaultCreditCard: 'https://backend-clone.sav-vy.com/oliv-api/default-card-checkout',
  addCreditCard: 'https://oliv.com/pricing'
